const thisYear = new Date();
const Footer = () => (
    <footer className="footer">
        <div className="footerContent">
            <p>&#x00A9; Daniel McGhee {thisYear.getFullYear()}</p>
        </div>
        <style jsx>
            {`
                .footerContent {
                    align-content: center;
                }
                .footer {
                    position: fixed;
                    bottom: 0;
                    left: 0;
                    text-align: center;
                    display: block;
                }
            `}
        </style>

    </footer>
);

export default Footer;